@import url(https://fonts.googleapis.com/css?family=Indie+Flower&display=swap);
body {
  background: #dee8ee;
  margin: 10px 0; }

.heading {
  font-family: "Indie Flower", cursive;
  background-color: #2d2d2a;
  color: #ffffff;
  height: 50px;
  font-size: 2.5em;
  margin-top: -10px;
  text-align: center;
  padding-top: 15px; }

.wrapper {
  display: flex; }

.editorWrap {
  width: 750px;
  margin-left: 20px;
  margin-top: 20px; }
  .editorWrap .title {
    display: inline-block;
    font-family: "Indie Flower", cursive;
    font-size: 2.5em;
    font-weight: bolder;
    color: #2d2d2a;
    padding: 10px; }
  .editorWrap textarea {
    background: #dee8ee;
    width: 100%;
    min-height: 100%;
    border: none;
    resize: none;
    outline: none;
    padding: 10px;
    font-size: 1.5em;
    color: #808080; }

.previewWrap {
  width: 550px;
  overflow-wrap: break-word;
  margin-top: 30px;
  margin-left: 9px; }
  .previewWrap #preview {
    background-color: #2d2d2a;
    color: #ffffff;
    width: 100%;
    padding: 0 25px;
    font-size: 1em;
    border: 1px solid #abc4d5;
    border-radius: 30px;
    box-shadow: 1px 1px 10px 2px #808080;
    line-height: 1.3; }

@media screen and (max-width: 650px) {
  body {
    margin: 5px 0; }
  .wrapper {
    display: flex;
    flex-direction: column; }
  .editorWrap {
    width: 350px;
    height: 350px;
    margin-left: 5px; }
    .editorWrap .title {
      font-size: 1.5em; }
    .editorWrap textarea {
      font-size: 1.2em; }
  .previewWrap {
    width: 310px;
    padding-top: 80px; }
    .previewWrap #preview {
      width: 100%; }
      .previewWrap #preview img {
        height: 200px; } }

#preview blockquote {
  border-left: 3px solid #ffffff;
  padding-left: 5px;
  margin-left: 25px; }

#preview code {
  padding: 1px 4px 2px 4px;
  font-size: 18px;
  font-weight: bold; }

#preview a {
  color: #add8e6; }

#preview pre {
  background-color: #78a1bb;
  padding: 5px 0 5px 5px;
  white-space: pre-wrap; }

#preview h1 {
  border-bottom: 2px solid #ffffff; }

#preview h2 {
  border-bottom: 1px solid #ffffff; }

#preview table {
  border-collapse: collapse; }

#preview td,
#preview th {
  border: 2px solid #ffffff;
  padding-left: 5px;
  padding-right: 5px; }

